<template>
    <header class="navbar navbar-static-top">
        <div class="container">
            <div class="navbar-header" v-show="currentProject!=='Home'">
                <a href="javascript:void(0);" class="navbar-brand" @click="linkTo('home')">{{currentProject}}</a>
            </div>
            <nav id="bs-navbar" class="collapse navbar-collapse">
                <ul class="nav navbar-nav">
                    <li id="mnuDemo" v-show="currentProject!=='Home'">
                        <a href="javascript:void(0);" @click="linkTo('demo')">Demo / Documents</a>
                    </li>
                    <li id="mnuDocs" v-show="currentProject!=='Home'">
                        <router-link to="/changelog">ChangeLog</router-link>
                    </li>
                </ul>
                <ul class="nav navbar-nav navbar-right">
                    <li>
                        <a href="javascript:void(0);" id="lnkMyProjects" >
                            <!--<i class="fa fa-fw fa-desktop"></i> Projects <i class="fa fa-fw fa-caret-down"></i>-->
                            <label v-show="currentProject!=='Home'">Project: </label>
                            <select @change="projectChange" v-model="currentProject">
                                <option :value="value" v-for="(value, key, index) in projects">{{value}}</option>
                            </select>
                        </a>
                    </li>
                    <!--
                    <li>
                        <a href="https://github.com/TerryZ" target="_blank">
                            <i class="fa fa-fw fa-code"></i> jQuery series plugins
                        </a>
                    </li>
                    -->
                    <li>
                        <a href="https://github.com/TerryZ" target="_blank">
                            <i class="fa fa-fw fa-github"></i> View on Github
                        </a>
                    </li>
                </ul>
            </nav>
        </div>
    </header>
</template>

<script>
    import constants from '../constants';
    let {projects} = constants;
    export default {
        name: "Header",
        data(){
            return {
                currentProject: 'Home',
                projects,
                url:{
                    home: '',
                    demo: ''
                }
            };
        },
        methods:{
            projectChange(){
                //console.log(this.currentProject);
                this.$store.commit('changeProject', this.currentProject);
                if(this.currentProject === 'Home'){
                    this.$router.push({path:'/'});
                }else{
                    let url = this.$store.state.urls[this.currentProject];
                    this.url.home = url.home;
                    this.url.demo = url.demo;
                    this.$router.push({path: url.home});
                }
            },
            linkTo(type){
                if(type) this.$router.push({path: this.url[type]});
            }
        },
        mounted(){
            if(this.$store.state.currentProject === 'Home')
                this.$router.push({path:'/'});
        }
    }
</script>

<style lang="scss" scoped>
    .navbar{
        margin-bottom: 0;
    }
    header{
        background-color: white;
        color: #666666;
        font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif;
    }
    .navbar-nav > li > a {
        color: #337ab7;
        &:hover, &:focus {
            color: #23527c;
            background-color: transparent;
        }
    }
    .navbar-nav > .active > a,
    .navbar-nav > .active > a:hover,
    .navbar-nav > .active > a:focus {
        color: #555;
        background-color: #e7e7e7;
    }
</style>


