import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';

//plugins
import vDialog from 'v-dialogs';
import vPage from 'v-page';
import vTableGrid from 'v-tablegrid';

Vue.use(router);

Vue.use(vDialog);
Vue.use(vPage);
Vue.use(vTableGrid);

new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App)
});


